@keyframes main-animation {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(0.5);
  }
}

@keyframes height-s {
  from {
    height: 100%;
  }
  to {
    height: 100vh;
  }
}

@keyframes appear {
  from {
    opacity: 0;
    transform: translateX(35%);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

@keyframes hide {
  from {
    opacity: 1;
    transform: translateX(0px);
  }
  to {
    opacity: 0;
    transform: translateX(35%);
  }
}

@keyframes scroll-lead {
  0% {
    transform: scale(1, 0);
    transform-origin: 0 0;
  }
  50% {
    transform: 1, 1;
    transform-origin: 0 0;
  }
  51% {
    transform: scale(1, 1);
    transform-origin: 0 100%;
  }
  100% {
    transform: scale(1, 0);
    transform-origin: 0 100%;
  }
}

@keyframes block-appear {
  from {
    opacity: 0;
    transform: translateY(10%);
  }
  to {
    opacity: 1;
    transform: translateY(0%);
  }
}

/*font-size, font-color, line-height指定*/
.cursor {
  position: absolute;
  transform: scale(0.8);
  z-index: 1;
  width: 8px;
  height: 8px;
  border-radius: 100%;
  background: #ffffff;
  transition: 0.5s cubic-bezier(0.75, -1.27, 0.3, 2.33) transform, 0.2s cubic-bezier(0.75, -1.27, 0.3, 2.33) opacity;
  pointer-events: none;
}

.cursor.is-hover {
  opacity: 1;
  transform: scale(3);
}

.follower {
  position: absolute;
  transform: translate(4px, 4px);
  z-index: 1;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  background: rgba(198, 0, 105, 0.5);
  transition: 0.3s cubic-bezier(0.75, -1.27, 0.3, 2.33) transform, 0.4s cubic-bezier(0.75, -1.27, 0.3, 2.33) background;
  pointer-events: none;
}

.follower.is-hover {
  background: rgba(86, 0, 198, 0.5);
  transform: scale(2);
}

html {
  /*
    計算式：10px /  デザインカンプの横幅 * 100
    画面サイズがデザインカンプ幅の時font-size = 10pxになる
