.container {
  width: 100vw;
  transform-style: preserve-3d;
  overflow: hidden;
  background-color: #050523;
  position: relative;
}

.container:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 120%;
  height: 120%;
  overflow: hidden;
  filter: blur(5px) brightness(100%);
  z-index: -10;
  background: inherit;
}

.container.about {
  background-image: url("../images/about-backround@2x.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.container canvas {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: -1;
}

.container.service {
  background-image: url("../images/service-backround@2x.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.container.notes {
  background-image: url("../images/service-backround@2x.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.container.active .slides {
  animation: main-animation 0.5s ease cursor pointer;
  transform: perspective(130rem) rotateY(20deg) translateY(1rem) translateZ(31rem) scale(0.7);
}

@media (max-width: 768px) {
  .container.active .slides {
    transform: perspective(130rem) rotateY(20deg) translateZ(31rem) scale(0.5) translateX(-60%);
  }
}

.container.active .slides:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0));
}

.container.active .page-wrapper {
  animation: main-animation 0.5s ease cursor pointer;
  transform: perspective(130rem) rotateY(20deg) translateY(1rem) translateZ(31rem) scale(0.7);
  height: 100vh;
  max-height: 100vh;
  overflow: hidden;
}

@media (max-width: 768px) {
  .container.active .page-wrapper {
    transform: perspective(130rem) rotateY(20deg) translateZ(31rem) scale(0.5) translateX(-60%);
  }
}

.container.active .page-wrapper:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0));
}

.container.active .links a {
  animation: appear 0.8s forwards ease var(--i);
}

.container.active .content {
  display: none;
}

.links {
  position: absolute;
