.slides {
  position: relative;
  height: 100%;
  width: 100vw;
  background-color: #050523;
  transition: background 1s cubic-bezier(0.99, 1, 0.92, 1), transform 0.5s ease;
  transform-origin: left;
  transform-style: preserve-3d;
}

.is-sliding .slides {
  background-color: #050523;
  transition: background 0.3s cubic-bezier(0.99, 1, 0.92, 1);
}

.slides-nav {
  z-index: 99;
  position: fixed;
  right: 0%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 8.33335%;
}

@media (max-width: 375px) {
