.slides-nav button {
  display: block;
  width: 3.7rem;
  height: 3.4rem;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

@media (max-width: 375px) {
  .slides-nav button {
    width: 2.4rem;
    height: 2.2rem;
  }
}

.slides-nav .slides-nav__prev {
  margin-bottom: 13.6rem;
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.slides-nav .slides-nav__prev:hover {
  opacity: 0.8;
  transform: translateY(-10%);
  transition: opacity 0.3s ease, transform 0.3s ease;
}

@media (max-width: 375px) {
  .slides-nav .slides-nav__prev {
    margin-bottom: 8.8rem;
  }
}

.slides-nav .slides-nav__next {
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.slides-nav .slides-nav__next:hover {
  opacity: 0.8;
  transform: translateY(10%);
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.slides-nav span {
  font-size: 2.4rem;
